.header {
	--nav-background: url('../../assets/header_background.jpg');
	--nav-background-pos-v: 53%;

	padding-top: 43px;
	background: var(--nav-background, red) center var(--nav-background-pos-v), var(--light-green);
	background-size: cover;
	width: 100%;
	height: 240px;
	position: relative;
}
.header__wrapper {
	position: relative;
	box-shadow: 0 5px 15px 0 rgba(0, 0, 0, 0.432);
}
.wrapper__nav.white-overlay::before {
	z-index: -1;
}
.wrapper__nav {
	padding: 20px 15px 20px;
	height: 100%;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
	gap: 25px;
	/* box-shadow: inset 0 5px 5px 0 white; */
	background: linear-gradient(180deg, rgba(255, 255, 255, 0.548) 0%, transparent 35%);
}
.flex {
	display: flex;
	justify-content: space-between;
	align-items: center;
}
.flex--vertical{
	flex-direction: column;
}
.nav__flex-top,
.nav__flex-bottom {
	width: 80%;
}
.nav__flex-top {
	gap: 80px;
}
.nav__flex-bottom {
	justify-content: center;
	gap: 20px;
}

.nav__logo {
	width: 75px;
	min-width: 75px;
	height: fit-content;
}
.logo__svg {
	overflow: visible;
}
.nav__burger {
	z-index: 10;
	background: none;
	border: none;
	cursor: pointer;
}
