.header__input-container {
	display: none;
	margin: 20px auto;
	max-width: 531px;
	/* margin-top: 20px; */
}
.header__input-container input:focus + label,
.header__input-container.filled label {
	color: white;
}
/* prettier ignore */
.header__input-container {
	animation-delay: 0.3s;
}
