/* prettier ignore */
.nav__logo {
	animation-delay: 0.1s;
}
/* prettier ignore */
.nav__cart {
	animation-delay: 0.2s;
}
.nav__burger {
	animation-delay: 0.4s;
}
/* RWD */
@media screen and (min-width: 1200px) {
	.nav__logo {
		width: 110px;
		position: absolute;
		top: 50%;
		left: var(--top-padding-sides);
		transform: translate(-50%, -50%);
	}
	.nav__cart {
		position: absolute;
		top: 20px;
		right: var(--top-padding-sides);
	}
}
@media screen and (min-width: 600px) {
	.wrapper__nav {
		padding: 20px 20% 30px;
		justify-content: flex-end;
		--top-padding-sides: 200px;
	}
	ul.nav__menu-list {
		display: flex;
		margin: auto;
		width: 80%;
		justify-content: space-between;
		overflow: visible;
	}
	/* prettier-ignore */
	.nav__flex-bottom 				{	width: 100%; }
	/* prettier-ignore */
	.nav__input-container,
	.header__menu,
	button.nav__burger 				{	display: none; }
	/* prettier-ignore */
	.header 									{	height: 338px; }
	/* prettier-ignore */
	.header__wrapper 					{	height: 180px; }
	/* prettier-ignore */
	.header__blur-background	{	--nav-background-pos-v:calc(53% + 40px) }
	/* prettier-ignore */
	.header__input-container	{ display: block;	}
}
/* fix cover backgrounds mismatch on 315px breakpoint	 */
@media screen and (max-width: 350px) {
	.header__wrapper > .wrapper__blur-background {
		background-size: auto 133%;
	}
}
