.menu-item{
	letter-spacing: 5px;
}

.header__menu {
	width: 40%;
	min-width: 200px;
	position: absolute;
	right: 0;
	bottom: -110px;
	z-index: 10;
	overflow: hidden;
}
.header__menu-item {
	background-color: var(--light-green);
	margin-right: 15%;
	transition: 0.5s transform var(--jump-both-bezier), 0.8s opacity var(--jump-end-bezier);
}

.header__menu-item > a {
	width: 100%;
	padding: 10px 20px;
	display: inline-block;
	overflow: hidden;
}
/* prettier-ignore */
.header__menu-item:not(:last-child)	{	margin-bottom: 2px; }

.nav__menu-list {
	display: none;
	padding: 0;
	font-size: 1.2em;
}

.header__menu--hidden .menu-item {
	opacity: 0;
	transform: translateX(100%);
}
.header__menu--visible .menu-item {
	opacity: 1;
	transform: initial;
}
