.checkout{
	padding: 50px 200px;
}
.checkout__row{
	display: flex;
	gap: 50px;
}
.checkout__input-container > .input{
	background: white;
	border: 1px solid black;
}
.checkout.checkout__row+.checkout__row{
	margin-top: 20px;
}
@media screen and (min-width:600px) {
	.checkout__stepper{
		position: absolute;
		top: 270px;
		right: 50%;
		transform: translateX(50%);
		width: 50%;
	}
}