@import url('https://fonts.googleapis.com/css2?family=Comfortaa:wght@300;400;500;600;700&display=swap');
:root {
	--light-green: #dff6df;
	--green: hsl(120, 56%, 79%);
	--red: hsl(352, 70%, 55%);
	--light-red: hsl(355, 100%, 90%);
	--dark-green: hsl(120, 86%, 29%);
	--darkX-green: #232;
	--light-gray: hsl(0, 0%, 94%);
	--gray: hsl(0, 0%, 68%);
	--dark-gray: hsl(0, 0%, 20%);
	--overlay: rgba(255, 255, 255, 0.25);
	--overlay-dense: rgba(255, 255, 255, 0.56);
	--font-color: hsl(0, 0%, 20%);

	--quick-bezier: cubic-bezier(0.16, 0.79, 0.72, 1.08);
	--jump-end-bezier: cubic-bezier(0.23, 0.53, 0.71, 1.52);
	--jump-both-bezier: cubic-bezier(0.38, -0.57, 0.56, 1.48);

	--border-radius: 17px;
	--border-radius-s: 6px;
}

body {
	text-align: center;
	font-family: 'Comfortaa', Roboto;
	font-weight: 300;
	letter-spacing: 1.5px;
	color: var(--font-color);
}

/* h1,
h2,
h3,
h4 {
	font-family: 'italiana';
} */
.btn {
	border: none;
	cursor: pointer;
	padding: 15px 10px;
	border-radius: 10px;
	background-color: var(--green, red);
	user-select: none;
	color: inherit;
}
.btn:disabled {
	opacity: 0.6;
}
.btn--no-bg {
	background: none;
	padding: 0;
}
.white-overlay::before {
	content: '';
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	background-color: var(--overlay, red);
}
.white-overlay--dense::before {
	background-color: var(--overlay-dense, red);
}

ul[class] {
	list-style-type: none;
	margin: 0;
}
a {
	transition: 0.3s color var(--quick-bezier);
}
main {
	margin: 60px 6% 0;
}
.gallery {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	column-gap: 30px;
	row-gap: 10px;
}

.paper {
	box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
	border-radius: var(--border-radius-s);
	background-color: white;
}
