.cart-card{
	margin: 40px 5%;
	font-size: 18px;
	min-width: 230px;
}
.cart-card__title{
	background-color: var(--light-green);
	margin: 0;
	border-top-left-radius: inherit;
	border-top-right-radius: inherit;
	padding: 10px;
}
.cart-card__content{
	padding: 20px;
	flex-direction: column;
}
.cart-card__flex{
	gap: 40px;
}
.cart-card__quantity-widget{
	gap: 10px;
}
.cart-card__arrow{
	/* font-size: 1.3em; */
	--border-radius: var(--border-radius-s);
}
.arrow__background{
	padding: 3px;
}
.cart-card__cancel{
	font-size: 3em;
	color: var(--red);
	line-height: 1ch;
	overflow: hidden;
}
.cart-card__price{
	font-size: 1.2em;
}
.cart-card__img{
	border-radius: var(--border-radius);
}
.cart__summary, .summary__buttons{
	gap: 10px;
}
@media screen and (min-width: 340px) {
	.cart-card__content{
		flex-direction: row;
	}
}
@media screen and (min-width: 600px) {
	.cart-card{
		width: 60%;
		max-width: 600px;
		margin-left: auto;
		margin-right: auto;
	}
}