.cart__ico{
	padding: 10px;
	border-radius: 100%;
	overflow: visible;
	box-sizing: content-box;
	background-color: var(--light-green);
	margin-bottom: var(--price-margin);
}
.nav__cart{
	height: fit-content;
	--price-margin: 2px;
	font-size: .8rem;
	--popup-hover: 1.2;
	--popup-active: 1.3;
	width: 80px;
}
.nav__cart>a{
	position: relative;
}
.cart__counter{
	position: absolute;
	background-color: yellow;
	vertical-align: middle;
	bottom: var(--price-margin);
	left: -5px;
	width: 1.5em;
	line-height: 1.5em;
	overflow: visible;
	border-radius: 100%;
}
.cart__price{
	font-size: .8em;
}