.fancy-border {
	padding: var(--border-width, 1px);
	background: linear-gradient(150deg, #76bb76 20%, #dd3e54 100%);
	border-radius: calc(var(--border-radius) + var(--border-width, 1px));
}
.fancy-border__background, .fancy-border__btn {
	background: var(--background, white);
	height: 100%;
	border-radius: var(--border-radius);
}
.fancy-border__btn{
	padding: 3px;
}