.stepper{
	gap: 20px;
	--line-width: 2px;
}
.stepper__step{
	position: relative;
	border: var(--line-width) solid var(--dark-green);
	background-color: rgba(0, 0, 0, 0.253);
	color: var(--dark-green);
	display: flex;
	justify-content: center;
	align-items: center;
	width: 3em;
	height: 3em;
	border-radius: 50%;
	flex-shrink: 0;
	flex-grow: 0;
}
.step__title{
	position: absolute;
	top: -2em;
	right: 50%;
	transform: translateX(50%);
	color: white;
	width: 15em;
}
.step__number{
	font-size: 1.5em;
	user-select: none;
}
.stepper__line{
	height: 5px;
	border: 1px solid white;
	width: 100%;
	flex-shrink: 10;
	border-radius: 5px;
}
.stepper__step--current{
	color: white;
	background-color: var(--green);
	border: none;
}
.stepper__step--current ~ .stepper__step{
	color: white;
	border: var(--line-width) solid white;
	opacity: .5;
}
.stepper__step--current ~ .stepper__line{
	opacity: .5;
}