.product {
	padding: 5px 30px;
	font-size: 0.7em;
	width: 100%;
	--popup-hover: 1.05;
	--popup-focus: var(--popup-hover);
	/* min-width: 300px; */
	/* max-width: 329px; */
}
.product h1 {
	height: 2.5em;
}
.product__img {
	height: auto;
	object-fit: cover;
	flex-grow: 3;
	min-width: 100px;
}
.product__content {
	display: flex;
	justify-content: space-between;
	align-items: center;
	gap: 20px;
	margin: 0 -10px;
}
.product aside {
	min-width: 120px;
	box-sizing: content-box;
	font-size: 1rem;
	height: fit-content;
}
.product__btn {
	margin-top: 15px;
	--popup-hover: 1.2;
	--popup-focus: 1;
	--popup-active: 1.2;
}
.product__btn:hover {
	--popup-hover: 1.3;
	--popup-active: 1.1;
}
.product__available{
	font-size: .8em;
	margin-bottom: 15px;
}
@media screen and (min-width: 600px) {
	.product__content {
		flex-direction: column;
		height: calc(100%);
	}
	.product {
		width: 285px;
		height: 500px;
		height: fit-content;
	}
	.product__img {
		width: 100%;
	}
	.gallery {
		row-gap: 130px;
	}
}
