.prompt-background{
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	background-color: #aaa4;
	display: flex;
	align-items: center;
	justify-content: center;
	z-index: 1000;
}
.prompt{
	background-color: white;
	border-radius: 25px;
	padding: 30px 50px 20px;
	width: 300px;
}
.prompt__buttons{
	margin-top: 20px;
	display: flex;
	justify-content: space-between;
}
.prompt__error{
	height: 1em;
	padding: 8px;
	color: var(--red);
}