.wrapper{
	overflow: hidden;
	z-index: 2;
}
.wrapper__blur-background {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	background: red center var(--nav-background-pos-v);
	background: var(--nav-background, red) center var(--nav-background-pos-v);
	background-size: cover;
	/* background-attachment: fixed; */
	-webkit-filter: blur(4px);
	        filter: blur(4px);
	z-index: -1;
}
.cart__ico{
	padding: 10px;
	border-radius: 100%;
	overflow: visible;
	box-sizing: content-box;
	background-color: var(--light-green);
	margin-bottom: var(--price-margin);
}
.nav__cart{
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
	--price-margin: 2px;
	font-size: .8rem;
	--popup-hover: 1.2;
	--popup-active: 1.3;
	width: 80px;
}
.nav__cart>a{
	position: relative;
}
.cart__counter{
	position: absolute;
	background-color: yellow;
	vertical-align: middle;
	bottom: var(--price-margin);
	left: -5px;
	width: 1.5em;
	line-height: 1.5em;
	overflow: visible;
	border-radius: 100%;
}
.cart__price{
	font-size: .8em;
}
.input-container {
	position: relative;
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
}
.input-container > .input,
.input-container {
	width: 100%;
}
.input {
	border: none;
	padding: 8px;
	border-radius: var(--border-radius-s);
	background-color: var(--light-gray);
	text-indent: 1ch;
}

.input:invalid {
	background-color: var(--light-red);
}
.input + label {
	background: var(--green);
	position: absolute;
	/* color: var(--dark-gray, red); */
	transition: -webkit-transform 0.35s var(--jump-both-bezier);
	transition: transform 0.35s var(--jump-both-bezier);
	transition: transform 0.35s var(--jump-both-bezier), -webkit-transform 0.35s var(--jump-both-bezier);
	-webkit-transform-origin: bottom left;
	        transform-origin: bottom left;
	/* border: 1px solid black;
	border-right: none; */
	left: -1px;
	top: 0;
	padding: 9px;
	border-radius: var(--border-radius-s);
	cursor: text;
	/* width: 80%; */
}
.input-container:hover > label {
	border-radius: var(--border-radius-s);
	-webkit-transform: rotate(-45deg) scale(.7);
	        transform: rotate(-45deg) scale(.7);
}
.input-container>.input:hover{
	background-color: var(--light-gray);
}
.input:focus + label,
.input-container.filled > label {
	border-radius: var(--border-radius-s);
	-webkit-transform: translate(2em, 60%) scale(0.7);
	        transform: translate(2em, 60%) scale(0.7);
}
.input-container--label-in {
	color: var(--gray);
	padding-top: 1em;
	margin-top: -1em;
	/* max-width: 253px; */
	min-width: 150px;
}
.input-container--label-in > label {
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
	width: 80%;
	background: none;
	left: 50%;
	bottom: 9px;
	top: unset;
	padding: unset;
	border-radius: unset !important;
	-webkit-transform-origin: center !important;
	        transform-origin: center !important;
	-webkit-transform: translateX(-50%);
	        transform: translateX(-50%);
}

.input::-webkit-input-placeholder {
	-webkit-transition: 0.3s 0.2s opacity var(--quick-bezier);
	transition: 0.3s 0.2s opacity var(--quick-bezier);
	opacity: 0;
}

.input:-ms-input-placeholder {
	-ms-transition: 0.3s 0.2s opacity var(--quick-bezier);
	transition: 0.3s 0.2s opacity var(--quick-bezier);
	opacity: 0;
}

.input::placeholder {
	transition: 0.3s 0.2s opacity var(--quick-bezier);
	opacity: 0;
}
.input:focus::-webkit-input-placeholder, .input-container:hover > .input::-webkit-input-placeholder {
	opacity: 1;
}
.input:focus:-ms-input-placeholder, .input-container:hover > .input:-ms-input-placeholder {
	opacity: 1;
}
.input:focus::placeholder,
.input-container:hover > .input::placeholder {
	opacity: 1;
}
.input-container--label-in:hover > .input::-webkit-input-placeholder{
	opacity: 0;
}
.input-container--label-in:hover > .input:-ms-input-placeholder{
	opacity: 0;
}
.input-container--label-in:hover > .input::placeholder{
	opacity: 0;
}
.input-container--label-in:hover>label{
	-webkit-transform: translateX(-50%);
	        transform: translateX(-50%);
}
.input-container--label-in > .input:focus + label,
.input-container--label-in.filled label {
	-webkit-transform: translate(-50%, -140%) scale(0.8);
	        transform: translate(-50%, -140%) scale(0.8);
}
