.btn {
	border: none;
	cursor: pointer;
	padding: 15px 10px;
	border-radius: 10px;
	background-color: hsl(120, 56%, 79%);
	background-color: var(--green, red);
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	color: inherit;
}
.btn:disabled {
	opacity: 0.6;
}
.btn--no-bg {
	background: none;
	padding: 0;
}
.white-overlay::before {
	content: '';
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	background-color: rgba(255, 255, 255, 0.25);
	background-color: var(--overlay, red);
}
.white-overlay--dense::before {
	background-color: rgba(255, 255, 255, 0.56);
	background-color: var(--overlay-dense, red);
}

ul[class] {
	list-style-type: none;
	margin: 0;
}
a {
	transition: 0.3s color cubic-bezier(0.16, 0.79, 0.72, 1.08);
	transition: 0.3s color var(--quick-bezier);
}
main {
	margin: 60px 6% 0;
}
.gallery {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
	-webkit-justify-content: center;
	        justify-content: center;
	grid-column-gap: 30px;
	-webkit-column-gap: 30px;
	        column-gap: 30px;
	grid-row-gap: 10px;
	row-gap: 10px;
}

.paper {
	box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
	border-radius: 6px;
	border-radius: var(--border-radius-s);
	background-color: white;
}
