.cart-card{
	margin: 40px 5%;
	font-size: 18px;
	min-width: 230px;
}
.cart-card__title{
	background-color: var(--light-green);
	margin: 0;
	border-top-left-radius: inherit;
	border-top-right-radius: inherit;
	padding: 10px;
}
.cart-card__content{
	padding: 20px;
	-webkit-flex-direction: column;
	        flex-direction: column;
}
.cart-card__flex{
	grid-gap: 40px;
	gap: 40px;
}
.cart-card__quantity-widget{
	grid-gap: 10px;
	gap: 10px;
}
.cart-card__arrow{
	/* font-size: 1.3em; */
	--border-radius: var(--border-radius-s);
}
.arrow__background{
	padding: 3px;
}
.cart-card__cancel{
	font-size: 3em;
	color: var(--red);
	line-height: 1ch;
	overflow: hidden;
}
.cart-card__price{
	font-size: 1.2em;
}
.cart-card__img{
	border-radius: var(--border-radius);
}
.cart__summary, .summary__buttons{
	grid-gap: 10px;
	gap: 10px;
}
@media screen and (min-width: 340px) {
	.cart-card__content{
		-webkit-flex-direction: row;
		        flex-direction: row;
	}
}
@media screen and (min-width: 600px) {
	.cart-card{
		width: 60%;
		max-width: 600px;
		margin-left: auto;
		margin-right: auto;
	}
}
.fancy-border {
	padding: 1px;
	padding: var(--border-width, 1px);
	background: linear-gradient(150deg, #76bb76 20%, #dd3e54 100%);
	border-radius: calc(var(--border-radius) + 1px);
	border-radius: calc(var(--border-radius) + var(--border-width, 1px));
}
.fancy-border__background, .fancy-border__btn {
	background: white;
	background: var(--background, white);
	height: 100%;
	border-radius: var(--border-radius);
}
.fancy-border__btn{
	padding: 3px;
}
.stepper{
	grid-gap: 20px;
	gap: 20px;
	--line-width: 2px;
}
.stepper__step{
	position: relative;
	border: var(--line-width) solid var(--dark-green);
	background-color: rgba(0, 0, 0, 0.253);
	color: var(--dark-green);
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	width: 3em;
	height: 3em;
	border-radius: 50%;
	-webkit-flex-shrink: 0;
	        flex-shrink: 0;
	-webkit-flex-grow: 0;
	        flex-grow: 0;
}
.step__title{
	position: absolute;
	top: -2em;
	right: 50%;
	-webkit-transform: translateX(50%);
	        transform: translateX(50%);
	color: white;
	width: 15em;
}
.step__number{
	font-size: 1.5em;
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}
.stepper__line{
	height: 5px;
	border: 1px solid white;
	width: 100%;
	-webkit-flex-shrink: 10;
	        flex-shrink: 10;
	border-radius: 5px;
}
.stepper__step--current{
	color: white;
	background-color: var(--green);
	border: none;
}
.stepper__step--current ~ .stepper__step{
	color: white;
	border: var(--line-width) solid white;
	opacity: .5;
}
.stepper__step--current ~ .stepper__line{
	opacity: .5;
}
.checkout{
	padding: 50px 200px;
}
.checkout__row{
	display: -webkit-flex;
	display: flex;
	grid-gap: 50px;
	gap: 50px;
}
.checkout__input-container > .input{
	background: white;
	border: 1px solid black;
}
.checkout.checkout__row+.checkout__row{
	margin-top: 20px;
}
@media screen and (min-width:600px) {
	.checkout__stepper{
		position: absolute;
		top: 270px;
		right: 50%;
		-webkit-transform: translateX(50%);
		        transform: translateX(50%);
		width: 50%;
	}
}
